.signup-form {
  padding: 1rem;
  display: grid;
  grid-auto-rows: 2rem;
  row-gap: 0.5rem;
}
.signup-form label {
  font-weight: bold;
}
.signup-title {
  text-align: center;
  font-size: 1.8rem;
  color: #ff5454;
}

.signup-form label,
.signup-form input,
.signup-form select {
  display: block;
  width: 100%;
}

.signup-form input.valid,
.signup-form select.valid {
  border-color: red;
  background: #faacac;
}

/* .signup-form input[type="checkbox"] */
.signup-form input[id*='terms'],
.signup-form input[id*='terms'] + label {
  display: inline-block;
  width: auto;
  vertical-align: bottom;
}

.signup-form input,
.signup-form select {
  border: 1px solid #ccc;
  padding: 0.2rem 0.5rem;
  font: inherit;
}

.signup-form input:not([type='checkbox']):focus,
.signup-form select:focus {
  outline: none;
  background: #d8f3df;
  border-color: #2ddf5c;
}

.signup-form input[type='checkbox'] {
  border: 1px solid #ccc;
  background: white;
  width: 1rem;
  height: 1rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.signup-form input[type='checkbox']:checked {
  background: #2ddf5c;
  border: #0e4f1f;
}

/* add after other input styles */
.signup-form input.invalid,
.signup-form select.invalid,
.signup-form :invalid {
  border-color: red !important;
  background: #faacac;
}

.signup-form button[type='submit'] {
  display: block;
}

@media (min-width: 40rem) {
  .signup-form {
    margin: auto;
    width: 30rem;
    grid-template-columns: 10rem auto;
    row-gap: 1rem;
    column-gap: 0.5rem;
  }

  .signup-form [id='title'] {
    grid-column: span 2;
  }

  .signup-form__checkbox {
    grid-column: span 2;
  }
}
