.background {
  background: url('./images/plans-background.jpg') center/cover;
  filter: grayscale(50%);
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: -1;
  padding-top: 0;
}
.plan-box {
  width: 80%;
  margin: 1rem 0;
  // margin-bottom: 1rem;
  border: 4px solid #0e4f1f;
  border-left: none;
  padding: 0.9rem;
  position: relative;
  overflow: hidden;

  &:hover,
  &:active {
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    border-color: #ff5454;
  }

  a {
    text-decoration: none;
    color: inherit;
    display: block;
    padding: 2rem;
  }
}
.clearfix {
  clear: both;
}
#plus {
  background: rgba(213, 255, 220, 0.9);
}
#free {
  background: rgba(234, 252, 237, 0.9);
  float: right;
  border-right: none;
  border-left: 4px solid #0e4f1f;
  text-align: right;

  &:hover,
  &:active {
    border-left-color: #ff5454;
  }
}

#premium {
  background: rgba(14, 79, 31, 0.9);
  color: white;

  .plan__title {
    color: white;
  }
  .plan__subtitle {
    color: #bbb;
  }
}

.package__badge {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  font-size: 0.8rem;
  color: white;
  background: #ff5454;
  padding: 0.5rem;
  transform: rotateZ(45deg) translate(3.5rem, -1rem);
  transform-origin: center;
  width: 12rem;
  text-align: center;
}

.plan__subtitle {
  color: #979797;
}
.plan__desc {
  padding: 1rem;
  border: 1px solid #0e4f1f;
  font-size: 1.2rem;
  color: #0e4f1f;
  background: white;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  line-height: 1.5;
}
.free-plan__desc {
  text-decoration: line-through;
  text-decoration: underline dotted red;
}

@media (min-width: 40rem) {
  main {
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 1500px) {
  .plan-box {
    border-left: 4px solid #0e4f1f;
  }
  #free {
    border-right: 4px solid #0e4f1f;

    &:hover,
    &:active {
      border-right-color: #ff5454;
    }
  }
}
