@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;900&display=swap);
:root{--dark-green: #0e4f1f;--highlight-color: #ff1b68}*{box-sizing:border-box}html{height:100%}body{font-family:'Montserrat', sans-serif;margin:0;padding-top:3.5rem}@supports (display: grid){body{display:grid;grid-template-rows:3.5rem auto fit-content(8rem);grid-template-areas:'header' 'main' 'footer';padding-top:0;height:100%}}main{grid-area:main}.button{background:#0e4f1f;background:var(--dark-green);color:white;font:inherit;border:1.5px solid #0e4f1f;border:1.5px solid var(--dark-green);padding:0.5rem;border-radius:8px;font-weight:bold;cursor:pointer}.button:hover,.button:active{background:white;color:#0e4f1f;color:var(--dark-green)}.button:focus{outline:none}.button[disabled]{cursor:not-allowed;border:#a1a1a1;background:#ccc;color:#a1a1a1}.open{display:block !important;opacity:1 !important;transform:translateY(0) !important}.center{text-align:center}.slide-in-left-enter{transform:translateX(-100%)}.slide-in-left-enter-active{transform:translateX(0);opacity:1;transition:all 200ms}.slide-in-left-exit{transform:translateX(0%);opacity:1}.slide-in-left-exit-active{transform:translateX(-100%);opacity:0;transition:all 200ms}.pt-0{padding-top:0}.pt-1{padding-top:1rem}.pt-2{padding-top:2rem}.pt-3{padding-top:3rem}

.nav-links{list-style:none;margin:0;padding:0;width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.nav-links li{margin:1rem}.nav-links a{border:1px solid transparent;color:#292929;text-decoration:none;padding:0.5rem}.nav-links a:hover,.nav-links a:active,.nav-links a.active{background:#dadada;border-color:#292929;color:#292929}.nav-links button{cursor:pointer;border:1px solid #292929;color:#292929;background:transparent;padding:0.5rem;font:inherit}.nav-links button:focus{outline:none}.nav-links button:hover,.nav-links button:active{background:#292929;color:white}

.side-drawer{position:fixed;left:0;top:0;z-index:100;height:100vh;width:70%;background:white;box-shadow:0 2px 8px rgba(0,0,0,0.26)}

.backdrop{position:fixed;top:0;left:0;width:100%;height:100vh;background:rgba(0,0,0,0.75);z-index:10}

.main-header{width:100%;position:fixed;top:0;left:0;background:#2ddf5c;padding:0.5rem 1rem;z-index:60;display:flex;align-items:center;justify-content:space-between}.toggle-button{width:3rem;background:transparent;border:none;cursor:pointer;padding-top:0;padding-bottom:0;vertical-align:middle}.toggle-button:focus{outline:none}.toggle-button__bar{width:100%;height:0.2rem;background:black;display:block;margin:0.6rem 0}.main-header__brand{color:#0e4f1f;text-decoration:none;font-weight:bold}.main-header__brand img{height:2.5rem;vertical-align:middle}.main-nav{display:none}.main-nav__items{margin:0;padding:0;list-style:none;display:flex}.main-nav__item{margin:0 1rem}.main-nav__item a,.mobile-nav__item a{text-decoration:none;color:var(--dark-green);font-weight:bold;padding:0.2rem 0}.main-nav__item a:hover,.main-nav__item a:active{color:white;border-bottom:5px solid white}.main-nav__item--cta{animation:wiggle 400ms 3s 8 ease-out none}.main-nav__item--cta a,.mobile-nav__item--cta a{color:white;background:var(--highlight-color);padding:0.5rem 1rem;border-radius:8px}.main-nav__item--cta a:hover,.main-nav__item--cta a:active,.mobile-nav__item--cta a:hover,.mobile-nav__item--cta a:active{color:var(--highlight-color);background:white;border:none}.main-navigation__drawer-nav{height:100%}@media (min-width: 40rem){.toggle-button{display:none}.main-nav{display:flex}}.mobile-nav{display:none;position:fixed;z-index:101;top:0;left:0;background:white;width:80%;height:100vh}.mobile-nav__items{width:90%;height:100%;list-style:none;margin:0% auto;padding:0;display:flex;flex-direction:column;justify-content:center;align-items:center}.mobile-nav__item{margin:1rem 0}.mobile-nav__item a{font-size:1.5rem}@keyframes wiggle{0%{transform:rotateZ(0)}50%{transform:rotateZ(-10deg)}100%{transform:rotateZ(10deg)}}

.main-footer{background:black;padding:2rem;margin-top:3rem;grid-area:footer}.main-footer__links{list-style:none;margin:0;padding:0;display:flex;flex-direction:column;align-items:center}.main-footer__link{margin:0.5rem 0}.main-footer__link a{color:white;text-decoration:none}.main-footer__link a:hover,.main-footer__link a:active{color:#ccc}@media (min-width: 40rem){.main-footer__link{margin:0 1rem}.main-footer__links{flex-direction:row;justify-content:center;align-items:center}}

.modal{z-index:100;position:fixed;top:22vh;left:10%;width:80%;background:white;box-shadow:0 2px 8px rgba(0,0,0,0.26);border-radius:8px}.modal__header{width:100%;padding:1rem 0.5rem;background:#2a006e;color:white}.modal__header h2{margin:0.5rem}.modal__content{padding:1rem 0.5rem}.modal__footer{padding:1rem 0.5rem}.modal__actions{text-align:center}.modal__action{border:1px solid #0e4f1f;background:#0e4f1f;text-decoration:none;color:white;font:inherit;padding:0.5rem 1rem;margin-right:1rem;cursor:pointer}.modal__action:hover,.modal__action:active{background:#2ddf5c;border-color:#2ddf5c}.modal__action--negative{background:red;border-color:red}.modal__action--negative:hover,.modal__action--negative:active{background:#ff5454;border-color:#ff5454}@media (min-width: 768px){.modal{left:calc(50% - 20rem);width:40rem}}.modal-enter{transform:translateY(-10rem);opacity:0}.modal-enter-active{transform:translateY(0);opacity:1;transition:all 200ms}.modal-exit{transform:translateY(0);opacity:1}.modal-exit-active{transform:translateY(-10rem);opacity:0;transition:all 200ms}

#product-overview{background:linear-gradient(to top, rgba(80,68,18,0.6) 10%, transparent),url(/uhost-react/static/media/freedom.c88430a4.jpg) center/cover no-repeat border-box,rgba(255,27,104,0.407843);width:100vw;height:33vh;position:relative}#product-overview h1{color:white;font-family:'Anton', sans-serif;position:absolute;bottom:5%;left:3%;font-size:1.6rem}@media (min-width: 40rem) and (min-height: 40rem){#product-overview{height:40vh;background-position:50% 25%}#product-overview h1{font-size:3rem}}.section-title{color:darkgreen;text-align:center}.plan{background:#d5ffdc;text-align:center;padding:1rem;margin:0.5rem 0;width:100%}.plan--highlighted{background:#19b84c;color:white}.plan__badge{background:white;color:#19b84c;padding:0.5rem;border-radius:8px;box-shadow:2px 2px 2px 2px rgba(0,0,0,0.5)}.plan__title{color:#0e4f1f}.plan__price{color:#858585}.plan--highlighted .plan__title{color:white}.plan--highlighted .plan__price{color:#0e4f1f}.plan ul{list-style:none;margin:0;padding:0}.plan li{margin:8px 0}@media (min-width: 40rem){.plan__list{width:100%;display:flex;justify-content:center;align-items:center}.plan{width:30%;min-width:13rem;max-width:25rem;display:flex;flex-direction:column;justify-content:space-between;height:28rem}.plan--highlighted{box-shadow:2px 2px 2px 2px rgba(0,0,0,0.5);height:30rem;z-index:50}}#key-features{background:#ff1b68;margin-top:5rem;padding:1rem}#key-features .section-title{color:white;margin:2rem}.key-feature__list{list-style:none;margin:0;padding:0;text-align:center}.key-feature__item{width:100%}.key-feature__image{background:#ffcede;width:128px;height:128px;border:2px solid #424242;border-radius:50%;margin:auto;padding:1.5rem}.key-feature__desc{text-align:center;font-weight:bold;color:white;font-size:1.2rem}@media (min-width: 40rem){.key-feature__item{width:30%;max-width:25rem}.key-feature__list{display:flex;justify-content:center}}

.background{background:url(/uhost-react/static/media/plans-background.f4f158a2.jpg) center/cover;-webkit-filter:grayscale(50%);filter:grayscale(50%);width:100vw;height:100vh;position:fixed;z-index:-1;padding-top:0}.plan-box{width:80%;margin:1rem 0;border:4px solid #0e4f1f;border-left:none;padding:0.9rem;position:relative;overflow:hidden}.plan-box:hover,.plan-box:active{box-shadow:2px 2px 4px rgba(0,0,0,0.5);border-color:#ff5454}.plan-box a{text-decoration:none;color:inherit;display:block;padding:2rem}.clearfix{clear:both}#plus{background:rgba(213,255,220,0.9)}#free{background:rgba(234,252,237,0.9);float:right;border-right:none;border-left:4px solid #0e4f1f;text-align:right}#free:hover,#free:active{border-left-color:#ff5454}#premium{background:rgba(14,79,31,0.9);color:white}#premium .plan__title{color:white}#premium .plan__subtitle{color:#bbb}.package__badge{position:absolute;top:0;right:0;margin:0;font-size:0.8rem;color:white;background:#ff5454;padding:0.5rem;transform:rotateZ(45deg) translate(3.5rem, -1rem);transform-origin:center;width:12rem;text-align:center}.plan__subtitle{color:#979797}.plan__desc{padding:1rem;border:1px solid #0e4f1f;font-size:1.2rem;color:#0e4f1f;background:white;font-family:'Roboto', sans-serif;font-weight:400;line-height:1.5}.free-plan__desc{text-decoration:line-through;-webkit-text-decoration:underline dotted red;text-decoration:underline dotted red}@media (min-width: 40rem){main{max-width:1500px;margin-left:auto;margin-right:auto}}@media (min-width: 1500px){.plan-box{border-left:4px solid #0e4f1f}#free{border-right:4px solid #0e4f1f}#free:hover,#free:active{border-right-color:#ff5454}}

.testimonial{font-size:1.2rem}.testimonial__image-container{width:60%;max-width:40rem;box-shadow:3px 3px 5px 3px rgba(0,0,0,0.3);transform:skew(20deg);overflow:hidden}#customer-1:hover .testimonial__image-container{animation:flip-customer 1s ease-out forwards}.testimonial__image{width:100%;vertical-align:top;transform:skew(-20deg) scale(1.3)}.testimonial__info{text-align:right;padding:0.9rem}#customer-2.testimonial{text-align:right}#customer-2 .testimonial__info{text-align:left}.testimonial__name{margin:0.2rem;color:#ff5454;font-size:2rem}.testimonial__subtitle{margin:0;font-size:1.1rem;color:#ccc}.testimonial__subtitle a{color:inherit;text-decoration:none}.testimonial__subtitle a:hover,.testimonial__subtitle a:active{color:#7a7a7a}.testimonial__text{margin:0.2rem}@media (min-width: 40rem){.testimonial{margin:3rem auto;max-width:1500px;display:flex;align-items:center;justify-content:space-around}.testimonial__image-container{width:66%}.testimonial__info{width:30%}}@keyframes flip-customer{0%{transform:rotateY(0) skew(20deg)}50%{transform:rotateY(160deg) skew(20deg)}100%{transform:rotateY(360deg) skew(20deg)}}

.signup-form{padding:1rem;display:grid;grid-auto-rows:2rem;grid-row-gap:0.5rem;row-gap:0.5rem}.signup-form label{font-weight:bold}.signup-title{text-align:center;font-size:1.8rem;color:#ff5454}.signup-form label,.signup-form input,.signup-form select{display:block;width:100%}.signup-form input.valid,.signup-form select.valid{border-color:red;background:#faacac}.signup-form input[id*='terms'],.signup-form input[id*='terms']+label{display:inline-block;width:auto;vertical-align:bottom}.signup-form input,.signup-form select{border:1px solid #ccc;padding:0.2rem 0.5rem;font:inherit}.signup-form input:not([type='checkbox']):focus,.signup-form select:focus{outline:none;background:#d8f3df;border-color:#2ddf5c}.signup-form input[type='checkbox']{border:1px solid #ccc;background:white;width:1rem;height:1rem;-webkit-appearance:none;-moz-appearance:none;appearance:none}.signup-form input[type='checkbox']:checked{background:#2ddf5c;border:#0e4f1f}.signup-form input.invalid,.signup-form select.invalid,.signup-form :invalid{border-color:red !important;background:#faacac}.signup-form button[type='submit']{display:block}@media (min-width: 40rem){.signup-form{margin:auto;width:30rem;grid-template-columns:10rem auto;grid-row-gap:1rem;row-gap:1rem;grid-column-gap:0.5rem;-webkit-column-gap:0.5rem;column-gap:0.5rem}.signup-form [id='title']{grid-column:span 2}.signup-form__checkbox{grid-column:span 2}}

